//@extend-elements
//original selectors
//.icon-vertical ul li a, .icon-vertical ul li >span
%extend_1 {
	padding: 10px;
	color: $color_limed_spruce_approx;
	text-align: center;
	display: block;
}

//original selectors
//.action_tabs .btn.active, .action_tabs .btn:active
%extend_2 {
	background: $black none repeat scroll 0 0;
	border: medium none;
	color: $white;
	background-image: none;
	//Instead of the line below you could use @include box-shadow($shadow-1, $shadow-2, $shadow-3, $shadow-4, $shadow-5, $shadow-6, $shadow-7, $shadow-8, $shadow-9, $shadow-10)
	box-shadow: none;
	outline: 0 none;
}

//original selectors
//.open .dropdown-menu >li >a:focus, .dropdown-menu >li >a:hover
%extend_3 {
	background-color: $white;
	color: $black;
	text-decoration: none;
	font-weight: bold;
}

//original selectors
//.action_tabs .md-button.active, .action_tabs .md-button:active
%extend_4 {
	background: $black none repeat scroll 0 0;
	border: medium none;
	color: $white;
	background-image: none;
	//Instead of the line below you could use @include box-shadow($shadow-1, $shadow-2, $shadow-3, $shadow-4, $shadow-5, $shadow-6, $shadow-7, $shadow-8, $shadow-9, $shadow-10)
