.image-builder-container {
	position: relative;
	.image-builder {
		background: $color_pumice_approx;
		//Instead of the line below you could use @include box-sizing($bs)
		box-sizing: border-box;
		border: 1px solid $color_pumice_approx;
		min-height: 460px;
		text-align: center;
		position: relative;
		overflow-x: scroll;
		overflow-y: hidden;
		width: auto;
		.image-cover {
			left: 0;
			right: 0;
			bottom: 0;
			top: 0;
			position: absolute;
			z-index: 2;
		}
		.fabric-container {
			background: $white;
			//Instead of the line below you could use @include border-radius($radius, $vertical-radius)
			border-radius: 1px;
			//Instead of the line below you could use @include box-shadow($shadow-1, $shadow-2, $shadow-3, $shadow-4, $shadow-5, $shadow-6, $shadow-7, $shadow-8, $shadow-9, $shadow-10)
			box-shadow: 0 1px 4px -1px $black_40;
			display: inline-block;
			margin: 50px;
			position: relative;
			vertical-align: middle;
			z-index: 0;
		}
	}
	.image-loading {
		background: $white_40;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		z-index: 1;
		.loading-indicator {
			background: $white;
			background-image: $url_0;
			background-repeat: no-repeat;
			background-position: center;
			//Instead of the line below you could use @include border-radius($radius, $vertical-radius)
			border-radius: 10px;
			//Instead of the line below you could use @include box-shadow($shadow-1, $shadow-2, $shadow-3, $shadow-4, $shadow-5, $shadow-6, $shadow-7, $shadow-8, $shadow-9, $shadow-10)
			box-shadow: 0 1px 3px $black_20;
			margin: 300px auto 0;
			height: 160px;
			width: 160px;
		}
	}
	.object-controls-container {
		position: relative;
		.object-controls {
			position: absolute;
			z-index: 1;
			background: $white;
			left: -250px;
			top: 0;
			padding: 5px;
			width: 240px;
			textarea {
				font-size: 12px;
			}
		}
	}
}
