			&:hover {
				background: $color_sunglow_approx;
				text-decoration: none;
			}
		}
		> span {
			@extend %extend_1;
			&:hover {
				background: $color_sunglow_approx;
				text-decoration: none;
			}
		}
		&.saveObject {
			position: relative;
		}
		ul {
			margin: auto;
			position: absolute;
			right: 100%;
			top: 0;
			width: 100px;
		}
		&:hover {
			background: $color_sunglow_approx;
			text-decoration: none;
		}
		&:last-child {
			border-bottom: none;
		}
	}
}
